.levelBarBLock {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 19em;
  margin: 10px auto;
}

.levelBar {
  width: 100%;
  height: 10px;
  background-color: #3a3e47;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.levelBarFill {
  height: 100%;
  background-color: #534caf;
  transition: 0.3s ease;
}

.count {
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  & svg {
    font-size: 18px;
  }
}
